{
    display:flex;
    flex-direction: column;
    gap:1rem;
    padding:16px;
    border-right: 1px solid var(--border-color);
}

.list-container .list-title
{
    font-size: 42px;
}

.toggle-tasks-notes-container
{
    display: flex;
    gap:0.5rem;
}

.toggle-add-task, .toggle-add-note
{
    align-self: flex-end;
    background-color: #1E1E1E;
    padding:4px 8px;
    border:0px;
    border-radius: 8px;
}

.toggle-add-task:hover, .toggle-add-note:hover
{
    cursor: pointer;
    background-color: var(--hover-color);
}

.active-add
{
    background-color: var(--active-color);
}

.add-task-form
{
    display:flex;
    align-items: center;
    gap:0.5rem;
}

.add-task-form .input-text
{
    background-color:var(--hover-color);
    flex:1 1 auto;
    font-size: 16px;
    padding:12px 16px;
    border-radius: 8px;
    border:0px;
}

.add-task-form .input-text:focus
{
    outline:1px solid #4772FA;
}


.date-toggle
{
    width: 20px;
    fill:var(--complete-button-color)
}

.date-toggle:hover
{
    cursor: pointer;
    fill:rgb(96, 94, 94);
}

.task-date-input
{
    background-color: var(--active-color);
}

.priority-flag-button
{
    width:20px;
    fill: var(--complete-button-color);
    cursor: pointer;
}

.priority-flag-button:hover
{
    fill:rgb(96, 94, 94);
}

.add-note-form
{
    display:flex;
    align-items: center;
    gap:0.5rem;
}

.add-note-form .input-text
{
    background-color:var(--hover-color);
    flex:1 1 auto;
    font-size: 16px;
    padding:12px 16px;
    border-radius: 8px;
    border:0px;
}

.add-note-form .input-text:focus
{
    outline:1px solid #4772FA;
}

.priority-container
{
    display: flex;
    gap:0.5rem;
    align-items: center;
}

.priority-container input[type='radio']:after
{
    accent-color: var(--complete-button-color);
}

.priority-container input[value="Low"]
{
    color:#4772FA;
}

.priority-container .low
{
    color:#4772FA;
}

.hidden
{
    display: none;
}

.notes-tasks-container
{
    display: flex;
    flex-direction: column;
    gap:1rem;
}

.task-card
{
    display: flex;
    align-items: center;
    padding:4px 8px;
    border-radius: 8px;
    gap:0.5rem;
    border-bottom: 1px solid var(--border-color);
}

.show-subtasks-button
{
    width: 15px;
    fill:var(--complete-button-color);
    cursor:pointer;
}

.close-subtasks-button
{
    width:15px;
    fill:var(--complete-button-color);
    cursor:pointer;
}

.task-card:hover
{
    background-color: var(--hover-color);
}

.task-card .title
{
    flex: 1 1 auto;
    font-size: 20px;
}

.task-card .delete-button
{
    width: 20px;
}

.task-card .delete-button:hover
{
    fill:rgb(125, 2, 2);
}

.completed-task
{
    background-color: var(--active-color);
    opacity: 40%;
}

.complete-unchecked-button
{
    width:22px;
    fill:var(--complete-button-color);
    cursor: pointer;
}

.complete-unchecked-button.low-priority
{
    fill:#4772FA;
}

.complete-unchecked-button.medium-priority
{
    fill:rgb(180, 180, 0);
}


.complete-unchecked-button.high-priority
{
    fill:rgb(255, 78, 78);
}


.complete-checked-button
{
    width:22px;
    fill:var(--complete-button-color);
    cursor: pointer;
}

.task-date
{
    color:#4772FA;
    font-size: 18px;
}


.note-card
{
    display: flex;
    align-items: center;
    gap:0.5em;
    padding:4px 8px;
    border-radius: 8px;
    border-bottom: 1px solid var(--border-color);
}

.note-card:last-child
{
    border:0px;
}

.note-card:hover
{
    background-color: var(--hover-color);
}


.note-icon
{
    width:20px;
    fill:var(--complete-button-color);
    margin-right: 7px;
}

.note-card .title
{
    flex: 1 1 auto;
    font-size: 20px;
}

.subtasks-container
{
    display: flex;
    flex-direction: column;
    margin-left: 30px;
    gap:0.5rem;

}

.hidden
{
    display:none;
}`, "",{"version":3,"sources":["webpack://./src/style/main.css"],"names":[],"mappings":"AAAA;;IAEI,YAAY;IACZ,sBAAsB;IACtB,QAAQ;IACR,YAAY;IACZ,2CAA2C;AAC/C;;AAEA;;IAEI,eAAe;AACnB;;AAEA;;IAEI,aAAa;IACb,UAAU;AACd;;AAEA;;IAEI,oBAAoB;IACpB,yBAAyB;IACzB,eAAe;IACf,UAAU;IACV,kBAAkB;AACtB;;AAEA;;IAEI,eAAe;IACf,oCAAoC;AACxC;;AAEA;;IAEI,qCAAqC;AACzC;;AAEA;;IAEI,YAAY;IACZ,mBAAmB;IACnB,UAAU;AACd;;AAEA;;IAEI,mCAAmC;IACnC,aAAa;IACb,eAAe;IACf,iBAAiB;IACjB,kBAAkB;IAClB,UAAU;AACd;;AAEA;;IAEI,yBAAyB;AAC7B;;;AAGA;;IAEI,WAAW;IACX;AACJ;;AAEA;;IAEI,eAAe;IACf,oBAAoB;AACxB;;AAEA;;IAEI,qCAAqC;AACzC;;AAEA;;IAEI,UAAU;IACV,kCAAkC;IAClC,eAAe;AACnB;;AAEA;;IAEI,oBAAoB;AACxB;;AAEA;;IAEI,YAAY;IACZ,mBAAmB;IACnB,UAAU;AACd;;AAEA;;IAEI,mCAAmC;IACnC,aAAa;IACb,eAAe;IACf,iBAAiB;IACjB,kBAAkB;IAClB,UAAU;AACd;;AAEA;;IAEI,yBAAyB;AAC7B;;AAEA;;IAEI,aAAa;IACb,UAAU;IACV,mBAAmB;AACvB;;AAEA;;IAEI,0CAA0C;AAC9C;;AAEA;;IAEI,aAAa;AACjB;;AAEA;;IAEI,aAAa;AACjB;;AAEA;;IAEI,aAAa;AACjB;;AAEA;;IAEI,aAAa;IACb,sBAAsB;IACtB,QAAQ;AACZ;;AAEA;;IAEI,aAAa;IACb,mBAAmB;IACnB,eAAe;IACf,kBAAkB;IAClB,UAAU;IACV,4CAA4C;AAChD;;AAEA;;IAEI,WAAW;IACX,iCAAiC;IACjC,cAAc;AAClB;;AAEA;;IAEI,UAAU;IACV,iCAAiC;IACjC,cAAc;AAClB;;AAEA;;IAEI,oCAAoC;AACxC;;AAEA;;IAEI,cAAc;IACd,eAAe;AACnB;;AAEA;;IAEI,WAAW;AACf;;AAEA;;IAEI,mBAAmB;AACvB;;AAEA;;IAEI,qCAAqC;IACrC,YAAY;AAChB;;AAEA;;IAEI,UAAU;IACV,iCAAiC;IACjC,eAAe;AACnB;;AAEA;;IAEI,YAAY;AAChB;;AAEA;;IAEI,qBAAqB;AACzB;;;AAGA;;IAEI,qBAAqB;AACzB;;;AAGA;;IAEI,UAAU;IACV,iCAAiC;IACjC,eAAe;AACnB;;AAEA;;IAEI,aAAa;IACb,eAAe;AACnB;;;AAGA;;IAEI,aAAa;IACb,mBAAmB;IACnB,SAAS;IACT,eAAe;IACf,kBAAkB;IAClB,4CAA4C;AAChD;;AAEA;;IAEI,UAAU;AACd;;AAEA;;IAEI,oCAAoC;AACxC;;;AAGA;;IAEI,UAAU;IACV,iCAAiC;IACjC,iBAAiB;AACrB;;AAEA;;IAEI,cAAc;IACd,eAAe;AACnB;;AAEA;;IAEI,aAAa;IACb,sBAAsB;IACtB,iBAAiB;IACjB,UAAU;;AAEd;;AAEA;;IAEI,YAAY;AAChB","sourcesContent":[".list-container\n{\n    display:flex;\n    flex-direction: column;\n    gap:1rem;\n    padding:16px;\n    border-right: 1px solid var(--border-color);\n}\n\n.list-container .list-title\n{